export function generateAuthError(message) {
    switch (message) {
        case "EMAIL_NOT_FOUND":
            return "   e-mail  ";
        case "INVALID_PASSWORD":
            return "Email    ";
        case "EMAIL_EXISTS":
            return "   email  ";
        case "USER_NOT_FOUND":
            return "  ";
        default:
            return "   .  ";
    }
}
